.course-card {
  border: none;
  border-radius: 0px;

  .card-body {
    padding: 0.5rem;

    .course-wrapper {
      display: flex;
      flex-direction: row;
      align-items: center;

      .course-image {
        width: 3rem;
        height: 3rem;
        display: flex;
        align-items: center;
        justify-content: center;

        img {
          width: 100%;
          height: 100%;
          overflow: hidden;
        }
      }

      .course-info {
        flex: 1;

        .course-overview {
          line-height: 1.4rem;
        }
      }

      .action-panel {
        display: flex;
        justify-content: space-evenly;

        .action-button {
          width: 2.5rem;
          height: 2.5rem;
          background: #ebebeb;
          border-radius: 13rem;
          display: flex;
          align-items: center;
          justify-content: center;
          margin: 0 5px;
        }
      }
    }
  }
}
